<template>
  <a-table :columns="columns" :dataSource="data" bordered>
    <template slot="name" slot-scope="text">
      <a href="javascript:;">{{text}}</a>
    </template>
  </a-table>
</template>
<script>
const columns = [
  {
    title: "客户名称",
    dataIndex: "name",
    scopedSlots: { customRender: "name" }
  },
  {
    title: "公司地址",
    dataIndex: "address"
  },
  {
    title: "联系人",
    dataIndex: "contacts"
  },
  {
    title: "联系人电话",
    dataIndex: "teleContact"
  },
  {
    title: "邮箱",
    dataIndex: "email"
  }
];

const data = [
  {
    key: "1",
    name: "John Brown",
    money: "￥300,000.00",
    address: "New York No. 1 Lake Park"
  },
  {
    key: "2",
    name: "Jim Green",
    money: "￥1,256,000.00",
    address: "London No. 1 Lake Park"
  },
  {
    key: "3",
    name: "Joe Black",
    money: "￥120,000.00",
    address: "Sidney No. 1 Lake Park"
  }
];

export default {
  data() {
    return {
      data,
      columns
    };
  }
};
</script>
<style>
th.column-money,
td.column-money {
  text-align: right !important;
}
</style>